<template>
    <div class="pr-2">
        <div v-if="ready">
          <button type="button" class="btn btn-sm btn-dark " style="position:absolute;z-index:10006;right:13px;margin-top:12px;" @click="shrink=!shrink">
                <img src="https://upload.egov.balangankab.go.id/svg/th-list.svg" 
                    class="d-inline " style="height:13px;width:13px;filter: brightness(0) invert(1);" alt="">
            </button>
          <div class="row justify-content-center">
              <div class="order-2 sm:order-1 shadow rounded-lg p-3 col-12"  >
                <div id="mapid" style="height:100vh;width:100%;background:white;">
                    
                </div>
              </div>
            <div v-if="shrink" class="" style="position:absolute;z-index:10000;right:10px;min-widht:300px;" >
                <div class="shadow rounded-lg p-3 scrollbarnya" style="min-height:80vh;overflow:scroll;background: rgb(200,222,219);
background: linear-gradient(90deg, rgba(200,222,219,1) 0%, rgba(38,185,215,0.7319970224417892) 100%);">
                 <button type="button" class="btn btn-sm btn-dark  float-right mr-5" @click="show3dmap">
                        <img src="https://upload.egov.balangankab.go.id/svg/map.svg" 
                            class="d-inline " style="height:13px;width:13px;filter: brightness(0) invert(1);" alt="">
                  </button>
                  <p class="font-semibold p-3 ">NAVIGATION <span class="typcn typcn-map"></span></p>
                  <hr class="style13 mt-3 mb-3">
                <div  class="  bg-white text-left p-4 shadow rounded-lg" style="font-size:13px;"
                  v-for="(item, index) in listPeta" :key="index+'listpeta'"
                  >
                  <img src="https://upload.egov.balangankab.go.id/svg/map.svg" 
                                class="d-inline " style="height:14px;width:14px;" alt="">
                  <span  class="pl-2">{{item.kategori}}</span>
                <hr class="mt-2 mb-2">
                <ul class="  anim mb-3  animated slideInLeft " >
                  <li class="bord  text-left p-1 " style="font-size:13px;" >
                    <p  @click="muncul($event);" class="p-1 mt-2 mb-2 hover:bg-green-400 anim rounded-lg">
                      <!-- note : checkedNames harus dalam bentuk array -->
                      <input type="checkbox" id="marker" value="marker" class=""  checked @click="loadmarker(item.id)">
                      <label for="marker">
                      </label>
                      <img src="https://upload.egov.balangankab.go.id/svg/pin.svg" 
                           class="d-inline" style="height:14px;width:14px;" alt="">Custom Marker 
                           <img src="https://upload.egov.balangankab.go.id/svg/arrow-down.svg" 
                                class="d-inline float-right" style="height:14px;width:14px;" alt="">
                    </p>
                    <div class="isaktif">
                      <div v-if="item2.icon" v-for="(item2, index2) in listMarker" :key="index2+'icon'" v-show="item2.id_peta==item.id" class="hover:bg-green-400 text-xs pl-3"
                      >
                        <img :src="item2.icon" class="d-inline" style="height:30px;width:14px;" alt=""> <img src="https://upload.egov.balangankab.go.id/svg/arrow-right.svg" 
                                class="d-inline" style="height:14px;width:14px;" alt=""> {{item2.title}}
                      </div>
                    </div>
                  </li>
                  <li>
                    <hr class="mt-2 mb-2">
                  </li>
                  <li class="bord  text-left p-3 " style="font-size:13px;" >
                    <p  class="mb-2">
                      <img src="https://upload.egov.balangankab.go.id/svg/map.svg" 
                                class="d-inline " style="height:14px;width:14px;" alt=""> Pilihan/Route
                    </p>
                    <div class="cursor-pointer">
                      <div
                      v-for="(item3, index3) in listGeojson" :key="index3+'icon'" v-show="item3.id_peta==item.id" class="hover:bg-green-400 cursor-pointer"
                      >
                        <input type="checkbox" :id="`pilihan${index3}`"  @click="loadgeojson(item3)" v-model="layers['geojson'+item3.id+'on']" class="ml-3 cursor-pointer">
                        <label :for="`pilihan${index3}`"><img src="https://upload.egov.balangankab.go.id/svg/arrow-right.svg" 
                                class="d-inline ml-1" style="height:14px;width:14px;" alt=""> {{item3.title}}</label>
                      </div>
                    </div>
                  </li>
                </ul>
              </div>
              </div>
            </div>
           
          </div>
        </div>
        <div class="row">
          <div class="col-12">
            <div v-if="show3d">
              <div id="live3d" v-if="vdata.link3d">
                <hr>
                <p class="text-lg text-red-400 text-center">Untuk melihat tampilan 3D klik tombol di bawah ini !</p>
                <p class="text-center text-lg font-semibold text-red-400 p-3">Click 3D <img src="https://upload.egov.balangankab.go.id/svg/arrow-down.svg" 
                     class="d-inline" style="height:20px;width:20px;" alt=""></p>
                <iframe :src="vdata.link3d" 
                style="height:100vh;width:100vw;"
                title="W3Schools Free Online Web Tutorials">
                  </iframe>
              </div>
            </div>
          </div>
        </div>
    </div>
</template>
<script>
import autophp from "@/plugins/autophp.js"; // ganti source nya
import _ from 'lodash'
import axios from 'axios'
let sdb = new autophp();
let maps;
export default{
    data(){
        return{
          photoSize:[-30.8650, 155.2094], // penting  [height, width] , more minus height = more height
          ready:false,
          shrink:true,
          center:[-33.8650, 151.2094],
          loading:true,
          show3d:false,
          listPeta:[],
          listMarker:[],
          listGeojson:[],
          listgeojson:[],
          geojson:[],
          geoJsonLayer:{},
          layers:{},
          vdata:{},
          markers:{},
          listkategori:false,
        }
    },
    watch:{
     
    },
      methods: {
        events(data){
          alert(data)
        },
        bawah(e) {
          if(e.target.querySelectorAll('ul').length>0){
            e.target.querySelectorAll('ul')[0].classList.toggle('isaktif')
          }
        },
        muncul (e) {
          e.target.nextElementSibling.classList.toggle('isaktif')
        },
         parent(e){
          e.target.parentNode.click();
        },
        loadListPeta(){
          let that=this;
          sdb.collection("app_gis_kategori").doc().get("*",`where id='${this.$route.query.id}' order by id`).then(res => {
            that.listPeta=res;
            that.$forceUpdate();
          });
          sdb.collection("app_gis_marker").doc().get("*",`where tb1.id_peta='${this.$route.query.id}' order by id`).then(res => {
            that.listMarker=res;
            that.$forceUpdate();
          });
          sdb.collection("app_gis_geojson").doc().get("*",`where tb1.id_peta='${this.$route.query.id}' order by id`).then(res => {
            that.listGeojson=res;
            that.$forceUpdate();
          });
         sdb.collection("app_gis_marker",false).doc().select(`select floorplan,link3d from app_gis_marker where id_peta='${that.$route.query.id}'`).then(res => {
                console.log(res)
                if(res.length>0){
                    that.vdata.floorplan=res[0].floorplan
                    that.vdata.link3d=res[0].link3d
                    that.$forceUpdate();
                }
                that.loadmap()
            });
        },
        loadmarker(item){
          // console.log(item)
          let that=this;
          let data = this.listMarker
          data=data.filter((e)=>e.id_peta==item)
           if(data.length>0){
             data.forEach((e,i)=>{
              if ( maps.hasLayer(that.markers['marker'+e.id]) ){  // if the specified layer is visible on the map, it needs to be removed
                maps.removeLayer(that.markers['marker'+e.id])
                that.markers['marker'+e.id+'on']=false
                return
              }
              that.markers['marker'+e.id+'on']=true
              console.log('listicon',e)
              if(i==0){
                // maps.panTo(new L.LatLng(e.lat, e.lng))
              }
            // =================================================================
                if(e.kategori=='icon'){
                  console.log('icon',e)
                    var greenIcon = L.icon({
                        iconUrl: e.icon,
                        iconSize:     [28, 75], // size of the icon
                        iconAnchor:   [12, 54], // point of the icon which will correspond to marker's location
                        popupAnchor:  [-3, -26] // point from which the popup should open relative to the iconAnchor
                    });
                    if(e.popup=='click'){
                        that.markers['marker'+e.id]=L.marker([e.lat, e.lng],{icon:greenIcon}).bindPopup(`
                        <div class="text-xs">
                            <div class="font-bold uppercase">${e.title}</div>
                            <div class="text-justify">${e.deskripsi}</div>
                            ${e.gambar?`<a href="${e.gambar}" class="mt-2" target="__blank"><img src="${e.gambar}" class="mt-2" style="height:100px;width:100px;" /></a>`:''}
                        </div>
                        `, {maxWidth: 500}).addTo(maps)
                        .addEventListener('click',function(){
                              if(e.event!='false' && e.event){
                                  that.events(e.event)
                              }
                          })
                    }else{
                        that.markers['marker'+e.id]=L.marker([e.lat, e.lng],{icon:greenIcon}).bindTooltip(`
                        <div class="text-xs">
                            <div class="font-bold uppercase">${e.title}</div>
                            <div class="text-justify">${e.deskripsi}</div>
                            ${e.gambar?`<a href="${e.gambar}" class="mt-2" target="__blank"><img src="${e.gambar}" class="mt-2" style="height:100px;width:100px;" /></a>`:''}
                        </div>
                        `,{ permanent: false, sticky: true }).addTo(maps)
                        .addEventListener('click',function(){
                                    if(e.event!='false' && e.event){
                                        that.events(e.event)
                                    }
                                })
                    }
            // =================================================================
                }else if(e.kategori=='gambar'){
                  console.log('gambar',e)
                     var greenIcon = L.icon({
                        iconUrl: e.gambar,
                        iconSize:     [48, 45], // size of the icon
                        iconAnchor:   [12, 54], // point of the icon which will correspond to marker's location
                        popupAnchor:  [-3, -26] // point from which the popup should open relative to the iconAnchor
                    });
                    if(e.popup=='click'){
                        that.markers['marker'+e.id]=L.marker([e.lat, e.lng],{icon:greenIcon}).bindPopup(`
                        <div class="text-xs">
                            <div class="font-bold uppercase">${e.title}</div>
                            <div class="text-justify">${e.deskripsi}</div>
                            ${e.gambar?`<a href="${e.gambar}" class="mt-2" target="__blank"><img src="${e.gambar}" class="mt-2" style="height:100px;width:100px;" /></a>`:''}
                        </div>
                        `, {maxWidth: 500}).addTo(maps)
                        .addEventListener('click',function(){
                                    if(e.event!='false' && e.event){
                                        that.events(e.event)
                                    }
                                })
                    }else{
                        that.markers['marker'+e.id]=L.marker([e.lat, e.lng],{icon:greenIcon}).bindTooltip(`
                        <div class="text-xs">
                            <div class="font-bold uppercase">${e.title}</div>
                            <div class="text-justify">${e.deskripsi}</div>
                            ${e.gambar?`<a href="${e.gambar}" class="mt-2" target="__blank"><img src="${e.gambar}" class="mt-2" style="height:100px;width:100px;" /></a>`:''}
                        </div>
                        `,{ permanent: false, sticky: true }).addTo(maps)
                        .addEventListener('click',function(){
                                    if(e.event!='false' && e.event){
                                        that.events(e.event)
                                    }
                                })
                    }
            // =================================================================
                }else if(e.kategori=='radius'){
                  console.log('radius',e)
                    if(e.popup=='click'){
                          that.markers['marker'+e.id]=L.circle([e.lat, e.lng], {
                              color: "black",
                              fillColor: e.color,
                              fillOpacity: e.opacity,
                              radius: e.radius
                          }).bindPopup(`
                        <div class="text-xs">
                            <div class="font-bold uppercase">${e.title}</div>
                            <div class="text-justify">${e.deskripsi}</div>
                        </div>
                        `, {maxWidth: 500}).addTo(maps)
                        .addEventListener('click',function(){
                                    if(e.event!='false' && e.event){
                                        that.events(e.event)
                                    }
                                });
                    }else{
                        that.markers['marker'+e.id]=L.circle([e.lat, e.lng], {
                              color: "black",
                              fillColor: e.color,
                              fillOpacity: e.opacity,
                              radius: e.radius
                          }).bindTooltip(`
                      <div class="text-xs">
                          <div class="font-bold uppercase">${e.title}</div>
                          <div class="text-justify">${e.deskripsi}</div>
                      </div>
                      `,{ permanent: false, sticky: true }).addTo(maps)
                      .addEventListener('click',function(){
                                    if(e.event!='false' && e.event){
                                        that.events(e.event)
                                    }
                                });
                    }
                }
        // =================================================================
            })
            }
        },
        loadgeojson(item){
          axios.get(item.geojson).then(res=>{
              if ( maps.hasLayer(this.layers['geojson'+item.id]) ){  // if the specified layer is visible on the map, it needs to be removed
                maps.removeLayer(this.layers['geojson'+item.id])
                this.layers['geojson'+item.id+'on']=false
                return
              }
              this.layers['geojson'+item.id]=L.geoJSON(res.data).addTo(maps);
              this.layers['geojson'+item.id+'on']=true
              if(res.data.features[0].geometry.type=='Polygon'){
                maps.panTo(new L.LatLng(res.data.features[0].geometry.coordinates[0][0][1],res.data.features[0].geometry.coordinates[0][0][0]))
              }else{
                maps.panTo(new L.LatLng(res.data.features[0].geometry.coordinates[0][1],res.data.features[0].geometry.coordinates[0][0]))
              }
              this.loadGeojsonLayer(this.layers['geojson'+item.id])
              this.loading = false;
              this.$forceUpdate();
          })
        },
        loadmap(){
            let that=this;
            this.ready=true;
            maps=null;
            setTimeout(() => {
              var center = [-33.8650, 151.2094];
              maps = new L.Map('mapid');
              maps.setView([-32.8650, 153.2094], 8);
              L.tileLayer('',{
                  maxZoom: 20,
                  subdomains:['mt0','mt1','mt2','mt3']
                  }).addTo(maps);
              maps.on('click',function(e){
                  console.log(e)
                  that.posisi[0]=e.latlng.lat
                  that.posisi[1]=e.latlng.lng
                  
                  that.modal=true;
                  that.$forceUpdate();
              })
              console.log('vdata',that.vdata)
              var imageUrl = that.vdata.floorplan,
              imageBounds = [center, that.photoSize];

              L.imageOverlay(imageUrl, imageBounds).addTo(maps);
              that.loadmarker(that.$route.query.id);
            }, 1000);
        },
        loadGeojsonLayer(layer){
              layer.eachLayer(function(layer) {
                setTimeout(() => {
                  // MENGGANTI ICON SECARA DYNAMIC SESUAI GEOSJON.IO
                  if(layer['_icon']){
                      let marker=``;
                      if(layer.feature.properties['marker-symbol']){
                          marker=marker+'-'+layer.feature.properties['marker-symbol'];
                      }
                      layer['_icon'].src=`https://a.tiles.mapbox.com/v4/marker/pin-m${marker}+${layer.feature.properties['marker-color'].replace('#','')}@2x.png?access_token=pk.eyJ1IjoibWFwYm94IiwiYSI6ImNpejY4NXFhYTA2bTMyeW44ZG0ybXBkMHkifQ.gUGbDOPUN1v1fTs5SeOR4A`;
                      layer['_icon'].style.height='80px'
                      layer['_icon'].style.width='30px'
                  }
                  // MENGGANTI POLYGON SECARA DYNAMIC
                  if(layer.feature.geometry.type=="Polygon"){
                      console.log('polygon')
                      // ====================
                      layer['_path'].setAttribute("fill", layer.feature.properties.fill);
                      layer['_path'].addEventListener('mouseover',function(){
                       layer['_path'].setAttribute("fill", '#000');
                      })
                      layer['_path'].addEventListener('mouseout',function(){
                       layer['_path'].setAttribute("fill", layer.feature.properties.fill);
                      })
                      // ====================
                      layer['_path'].setAttribute("stroke-opacity", layer.feature.properties['stroke-opacity']);
                      layer['_path'].setAttribute("stroke-width", layer.feature.properties['stroke-width']);
                      layer['_path'].setAttribute("stroke", layer.feature.properties.stroke);
                      layer['_path'].setAttribute("fill-opacity", layer.feature.properties['fill-opacity']);
                      // console.log(layer['_path'].setAttribute("fill", "#fff"))
                  }
                  // MENGGANTI POLYGON SECARA DYNAMIC
                  if(layer.feature.geometry.type=="LineString"){
                      console.log('LineString',layer)
                      layer['_path'].setAttribute("stroke-opacity", layer.feature.properties['stroke-opacity']);
                      layer['_path'].setAttribute("stroke-width", layer.feature.properties['stroke-width']);
                      layer['_path'].setAttribute("stroke", layer.feature.properties.stroke?layer.feature.properties.stroke:layer.options.color);
                  }
              }, 1000);
              // TOOLTIP TEXT SAAT HOVER 
                let kecuali=['stroke','stroke-width','stroke-opacity','fill','fill-opacity','marker-color','marker-size','marker-symbol']
                let txt=`<table class="table table-sm table-striped">`;
                Object.keys(layer.feature.properties).filter(e=>{
                  if(!kecuali.includes(e)){
                    return e;
                  }
                }).forEach(e=>{
                  txt=txt+`
                  ${layer.feature.properties[e].indexOf('http')!=-1?`<div><a href="${layer.feature.properties[e]}" target="__blank"><img class="viewer" style="height:100px;width:100px;" src="${layer.feature.properties[e]}"></a></div>`:`
                    <tr><td class="capitalize">${e}</td><td>${layer.feature.properties[e]}</td></tr>                  
                  `}
                  `;
                })
                // layer.bindPopup(
                layer.bindTooltip(
                  txt+`</table>`,
                  { permanent: false, sticky: true }
                );
              });
        },
        show3dmap(){
          this.show3d=!this.show3d
          this.$forceUpdate();
          setTimeout(() => {
            document.getElementById("live3d").scrollIntoView()
          }, 3000);
        }
    },
    mounted() {
       this.loadListPeta();
       setTimeout(() => {
         document.querySelector('a[href="https://leafletjs.com"]').style.color='transparent';
       }, 5000);
        // setTimeout(() => {
        //     this.loadmap()
        // }, 2000);
    },
     metaInfo: {
        title: 'Add Marker dynamic',
        meta: [
         { charset: 'utf-8' },
         { name: 'viewport', content: 'width=device-width, initial-scale=1' }
        ],
        link: [
         { rel: 'stylesheet', href: 'https://unpkg.com/leaflet@1.7.1/dist/leaflet.css', integrity:'sha512-xodZBNTC5n17Xt2atTPuE1HxjVMSvLVW9ocqUKLsCC5CXdbqCmblAshOMAS6/keqq/sMZMZ19scR4PsZChSR7A==',crossorigin:'' },
        ],
        script: [
            { src: 'https://unpkg.com/leaflet@1.7.1/dist/leaflet.js', integrity: "sha512-XQoYMqMTK8LvdxXYG3nZ448hOEQiglfqkJs1NOQV44cWnUrBc8PkAOcXy20w0vlaXaVUearIOBhiXZ5V3ynxwA==", crossorigin: ''},
        ],
    },
}
</script>
<style scoped>
